html, body {
  background-color: #9b9fa2; }

body {
  background-color: #ffffff;
  color: #000000;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Helvetica, Arial, sans-serif"; }

@media screen and (min-width: 30em) {
  body {
    font-family: "AvenirNextCondensed", Arial, sans-serif;
    margin: 0 auto;
    width: 48em; } }

h1, h2 {
  font-family: "TwCenMT-Regular", ArialMT, Arial, sans-serif; }

h3, h4, h5, h6 {
  font-family: "TwCenMT-Bold", Arial-BoldMT, Arial, sans-serif; }

ul, li {
  list-style-type: none; }

.header {
  background-color: #03b8ad;
  clear: both;
  padding: 10px;
  overflow: auto;
  position: relative; }

.call-to-action {
  padding: 15px 10px; }

button,
[type="button"],
[type="reset"],
[type="submit"],
.call-to-action__item {
  background-color: #5bc5bb;
  border: none;
  border-radius: 2px;
  color: #ffffff;
  cursor: pointer;
  font-size: 16px;
  margin: 10px 0;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
  width: 100%; }

.menu {
  display: block; }
  .menu--header {
    background-color: #ffffff;
    display: none; }
  .menu-list {
    padding: 0;
    margin: 0;
    text-align: center; }
    .menu-list__item {
      display: block;
      padding: 10px 5px; }
    .menu-list__anchor {
      color: #000000;
      font-size: 18px;
      text-transform: uppercase; }

input[type="text"],
input[type="number"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="week"],
input[type="date"],
input[type="month"],
input[type="datetime"],
input[type="datetime-local"],
input[type="url"],
select {
  background-color: #ffffff;
  border: none;
  color: #03b8ad;
  font-size: 18px;
  padding: 10px;
  width: 100%; }

input[type="search"] {
  -webkit-appearance: textfield; }

fieldset {
  border: none;
  margin: 0; }

.languages {
  float: right; }
  .languages__anchor {
    color: #ffffff;
    display: inline-block; }

.logo-wrapper {
  float: left; }

.logo {
  color: #ffffff;
  display: block;
  font-size: 12px;
  padding: 5px;
  text-transform: uppercase;
  vertical-align: middle; }
  .logo img {
    max-width: 100%;
    height: auto;
    width: 120px; }
  .logo--left {
    text-align: left; }

nav {
  clear: both; }

.nav {
  clear: both; }
  .nav-list {
    padding: 0;
    margin: 0;
    float: left; }
    .nav-list__item {
      display: inline-block; }
    .nav-list__anchor {
      color: #ffffff;
      padding: 5px 10px;
      text-transform: uppercase; }
    .nav-list__label {
      color: #ffffff;
      text-transform: uppercase;
      padding: 5px 10px; }
      .nav-list__label:before {
        content: '';
        background: url("/static/img/icons/rwanda/dropdown-menu.png") no-repeat 0 0/100%;
        width: 25px;
        height: 25px;
        display: inline-block;
        vertical-align: text-top;
        margin: 0 10px 0 0; }

.search {
  padding: 5px 10px; }
  .search label {
    color: #ffffff;
    cursor: pointer;
    display: block;
    float: right;
    text-transform: uppercase; }
    @media screen and (min-width: 30em) {
      .search label {
        background-image: url("/static/img/icons/rwanda/search-icon.png"), url("/static/img/icons/rwanda/search-cion.svg");
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: 100%;
        height: auto;
        text-indent: -1000px;
        width: 30px; } }
    @media screen and (min-width: 30em) {
      .search label:visited {
        outline: none; } }
  .search .search__input {
    visibility: hidden;
    position: absolute;
    right: 10%; }
  .search .search__input + * {
    display: none; }
  .search .search__input:checked + * {
    display: block; }

@media screen and (min-width: 30em) {
  .nav-list__anchor:visited,
  .menu-list__anchor:visited,
  .nav-list__label:visited,
  .search__item:visited {
    outline: none; } }
